* {
  // font-family: 'Poppins', sans-serif;
  box-sizing: border-box;
}

.header {
  position: absolute;
  width: 550px;
  height: 91px;
  left: 0px;
  top: 0px;
  color: white;
  padding: 30px 25px;

  background: #21222a;
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: space-between;
}

.main {
  position: absolute;
  width: 550px;
  height: 459px;
  left: 0px;
  top: 91px;
  color: white;
  padding: 57px 27px;
  background: #282d35;
}
.logo-large {
  position: absolute;
  left: 418px;
  top: 107px;
}

nav > img {
  width: 30px;
}
nav > h3 {
  margin-right: auto;
  color: #61dafb;
  font-style: normal;
  font-weight: bold;
  font-size: 22.2535px;
  line-height: 27px;
  letter-spacing: -0.05em;
}
.main > h1 {
  font-size: 39px;
  margin-top: 0px;
  margin-bottom: 0px;
}
.main > ul > li {
  line-height: 35px;
  max-width: 350px;
}

.main > ul > li::marker {
  color: #61dafb;
  font-size: 25px;
}
.logo-large {
  background-color: transparent;
}
